.hero {
    background-color: hsl(256, 26%, 20%);
    text-align: center;
    color: white;
    letter-spacing: 1px;
}

.hero img {
    width: 100%;
}

.heroIntro {
    padding: 6rem 2rem;
    position: relative;
}

.heroIntro:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 10rem;
    width: 100%;
    background: url("../../assets/images/bg-pattern-intro-left-mobile.svg");
    background-repeat: no-repeat;
    background-size: contain;
}

.heroIntro:after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -11rem;
    height: 100%;
    width: 8rem;
    background: url("../../assets/images/bg-pattern-intro-right-mobile.svg");
    background-repeat: no-repeat;
    background-size: cover;
}